import request from '@/utils/request';

// --------  ozon订单相关 -------

// 获取ozon订单
export function getOzonOrderData(params) {
  return request({ url: `/ozon_order/getData`, method: 'get', params })
}

// 获取excel
export function getDataToExcel(params) {
  return request({
      url: '/ozon_order/getDataToExcel',
      method: 'get',
      params: params,
      responseType: 'blob', // 指定返回的是二进制文件
  })
}

// 获取超重ozon订单 getOverWeightOzonOrderData
export function getOverWeightOzonOrderData(params) {
  return request({ url: `/ozon_order/getOverWeightOzonOrderData`, method: 'get', params })
}

// 超重商品处理
export function delOzonOrderOfOverWeight(data) {
  return request({ url: `/ozon_order/delOzonOrderOfOverWeight`, method: 'post', data })
}

// 拆分+审核ozon订单/ 审核ozon订单
export function auditOzonOrder(data) {
  return request({ url: `/ozon_order/auditOrder`, method: 'post', data })
}

// 冻结ozon订单
export function freezeOrder(data) {
  return request({ url: `/ozon_order/freezeOrder`, method: 'post', data })
}

export function unfreezeOrder(data) {
  return request({ url: `/ozon_order/unfreezeOrder`, method: 'post', data })
}

// 获取采购/打包 提问的 有问题的采购单
export function getErrorPurchaseOrder(params) {
  return request({ url: `/purchase_order/operationGetErrorData`, method: 'get', params })
}
// 上传图片
export function uploadImage(data) {
  return request({
    url: `/pic/upload`,
    method: 'post',
    data,
    headers: {
      'Content-Type': 'multipart/form-data',
    },
  });
}

// --------  ozon商品相关 -------

// 获取ozon商品
export function getOzonProductData(params) {
  return request({ url: `/ozon_product/getData`, method: 'get', params })
}

// ozon商品绑定系统内产品
export function bindSystemProducts(data) {
  return request({ url: `/ozon_product/bindSystemProducts`, method: 'post', data })
}

// --------  系统内商品相关 -------

// 获取系统内商品信息
export function getSystemProductData(params) {
  return request({ url: `/system_product/getData`, method: 'get', params })
}

// 新增系统内商品
export function addSystemProduct(data) {
  return request({ url: `/system_product/addData`, method: 'post', data })
}

// 用1688数据新增系统内商品
export function addDataWith1688Data(data) {
  return request({ url: `/system_product/addDataWith1688Data`, method: 'post', data })
}

// 修改系统内商品
export function modifySystemProduct(data) {
  return request({ url: `/system_product/modifyData`, method: 'post', data })
}

// 删除系统内商品
export function deleteSystemProduct(data) {
  return request({ url: `/system_product/deleteData`, method: 'post', data })
}