<template>
  <div>
    <a-card title="系统内商品管理">
      <a-row :gutter="16">
        <a-col :span="24" style="max-width: 200px; margin-bottom: 12px;">
          <a-input v-model="searchForm.keyWord" placeholder="关键字查询..." allowClear @pressEnter="search" />
        </a-col>
        <a-col :span="24" style="width: 100px; margin-bottom: 12px;">
          <a-button type="primary" icon="search" @click="search">查询</a-button>
        </a-col>

        <a-col :span="24" style="width: 100px; margin-bottom: 12px;">
          <a-button type="primary" icon="edit" @click="add">新增</a-button>
        </a-col>

        <a-col :span="24" style="width: 100px; margin-bottom: 12px;">
          <a-button type="primary" icon="edit" @click="add1688">1688新增</a-button>
        </a-col>
      </a-row>

      <a-row style="margin-top: 12px;">
        <a-table size="small" :columns="columns" :dataSource="items" rowKey="id" :loading="loading" :pagination="pagination" @change="tableChange">
          <div slot="primary_image" slot-scope="text, record">
            <img :src="record.primary_image" style="width: 50px; height: 50px;" alt="" />
          </div>
          <div slot="purchase_link" slot-scope="text, record">
            <div v-for=" link,index in JSON.parse(record.purchase_link)" :key="index+100">
              <span> {{ index+1 }}. </span>
              <a :href="link.link" target="_blank"> {{ `${index+1}号链接` }} </a>
              <span>备注：</span>
              <span> {{ link.msg }} </span>
              <hr>
            </div>
          </div>
          <div slot="action" slot-scope="value, item">
            <a-button-group>
              <a-button icon="edit" size="small" @click="openFormModal(item)">修改</a-button>
              <a-popconfirm title="确定删除吗" @confirm="destroy(item.id)">
                <a-button type="danger" icon="delete" size="small">删除</a-button>
              </a-popconfirm>
              
            </a-button-group>
          </div>
        </a-table>
      </a-row>
    </a-card>
    <form-modal v-model="visible" :visible="visible" :form="targetItem" :type="type" @update="update" />
    <form-add-with-data v-model="add_with_data_visible" :visible="add_with_data_visible" :form="targetItem" @update="update" />
  </div>
</template>

<script>
import {getSystemProductData,deleteSystemProduct } from '@/api/operation'

export default {
  name: 'SystemProduct',
  components: {
    FormModal: () => import('./FormModal.vue'),
    FormAddWithData: () => import('./FormAddWithData.vue'),
  },
  data() {
    return {
      columns: [
        {
          title: '商品ID',
          dataIndex: 'id',
        },
        {
          title: '商品主图',
          dataIndex: 'primary_image',
          key: 'primary_image',
          scopedSlots: { customRender: 'primary_image' },
          width: '80px'
        },
        {
          title: 'system_sku',
          dataIndex: 'system_sku',
        },
        {
          title: '同批标识id',
          dataIndex: 'father_id',
        },
        {
          title: '参考重量',
          dataIndex: 'reference_weight',
        },
        {
          title: '参考价格',
          dataIndex: 'reference_cost',
        },
        {
          title: '采购链接',
          dataIndex: 'purchase_link',
          scopedSlots: { customRender: 'purchase_link' },
          width: '250px'
        },
        {
          title: '采购平台',
          dataIndex: 'purchase_platform',
        },
        {
          title: '供应商名称',
          dataIndex: 'supplier_name',
        },
        {
          title: '采购量',
          dataIndex: 'wait_for_purchase_quantity',
          customRender: (value, item, index) => {
            return  value != "0"?"-" + value:value
          } 
        },
        {
          title: '在篮量',
          dataIndex: 'in_basket_quantity',
        },
        {
          title: '在途量',
          dataIndex: 'in_transit_quantity',
        },
        {
          title: '库存量',
          dataIndex: 'stock_quantity',
        },
        {
          title: '出库量',
          dataIndex: 'out_stock_quantity',
        },
        // {
        //   title: '丢失量',
        //   dataIndex: 'loss_quantity',
        // },
        {
          title: '创建人',
          dataIndex: 'creator',
          customRender: (value, item, index) => {
            return value?value.name:"未绑定"
          } 
        },
        {
          title: '操作',
          dataIndex: 'action',
          scopedSlots: { customRender: 'action' },
          width: '156px'
        },
      ],
      searchForm: { start: 0, limit: 15, keyWord: ""},
      pagination: { current: 1, total: 0, pageSize: 15 },
      loading: false,
      items: [],

      visible: false,
      add_with_data_visible:false,
      targetItem: {},
      form: {},
      importLoading: false,
      type:"",
    };
  },
  computed: {
  },
  methods: {
    initialize() {
      this.list();
    },
    list() {
      this.loading = true;
      getSystemProductData(this.searchForm).then(data => {
        this.pagination.total = data.data.count;
        this.items = data.data.data;
      }).finally(() => {
        this.loading = false;
      });
    },
    update() {
      this.visible = false
      this.add_with_data_visible = false
      this.list();
    },
    search() {
      this.searchForm.start = 0;
      this.pagination.current = 1;
      this.list();
    },
    add(){
      this.type = "add",
      this.targetItem = {};
      this.visible = true;
    },
    openFormModal(item) {
      this.type = "modify",
      this.targetItem = { ...item };
      this.visible = true;
    },
    add1688() {
      this.targetItem = {};
      this.add_with_data_visible = true;
    },
    destroy(id) {
      deleteSystemProduct({ "system_product_id":id }).then(
        res => {
          this.$message.success(res.msg);
          this.list();
      });
    },
    tableChange(pagination, filters, sorter) {      
      this.searchForm.start = (pagination.current-1)*this.pagination.pageSize;
      this.pagination.current = pagination.current;
      this.list();
    },
  },
  mounted() {
    this.initialize();
  },
  activated() {
    this.initialize();
  },
}
</script>